@import "../styles/base.scss";
@import "../styles/mixins.scss";

.user-info {
  border-radius: 3px;
  margin: 0 auto;
  width: 1179px;
  transition: 500ms;

  .lds-ripple {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;

    .lds-ripple div {
      position: absolute;
      opacity: 1;
      border-radius: 50%;
      animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
    }
    .lds-ripple div:nth-child(2) {
      animation-delay: -0.5s;
    }
    @keyframes lds-ripple {
      0% {
        top: 36px;
        left: 36px;
        width: 0;
        height: 0;
        opacity: 1;
      }
      100% {
        top: 0px;
        left: 0px;
        width: 72px;
        height: 72px;
        opacity: 0;
      }
    }
  }

  &__wrapper {
    width: 100%;
    display: flex;
    background-color: #36394ba6;
    border-radius: 10px;
  }

  &__today {
    width: 420px;

    &-header {
      width: 100%;
      color: #bfc1c8;
      background-color: rgba(0, 0, 0, 0.1);
      padding: 10px;
      font-weight: 400;
      font-size: 14px;
    }

    & > h4 {
      font-size: 1.2857142857em;
      font-weight: 400;
      color: #bfc1c8;
      padding: 30px 20px 0 20px;
    }

    &-temp-icon {
      display: flex;
      align-items: center;
      padding: 0 0 0 20px;
      width: 100%;

      & > h1 {
        color: #ffffff;
        font-size: 6.2rem;
        padding-right: 50px;
      }

      & > img {
        height: 132px;
      }
    }

    &-button {
      @include button;
      background-color: #2667f5;
      margin-bottom: 10px;
      color: white;
    }

    &-additional {
      margin-left: 20px;
      margin-bottom: 20px;

      & > span:first-child {
        margin-right: 20px;
      }

      & > span {
        width: 100%;
        color: #bfc1c8;
        font-weight: 400;
        font-size: 14px;
      }
    }
  }

  &__nextdays {
    flex: 1 1 auto;
    display: flex;

    &-nextday {
      &:nth-child(odd) {
        background-color: rgba(0, 0, 0, 0.1);
      }
      display: flex;
      flex-direction: column;
      align-items: center;
      flex: 1 1 auto;

      &-header {
        text-align: center;
        width: 100%;
        color: #bfc1c8;
        background-color: rgba(0, 0, 0, 0.1);
        padding: 10px;
        font-weight: 400;
        font-size: 14px;
      }

      & > img {
        margin-top: 15px;
      }

      &-temperature {
        margin-top: 18px;
        text-align: center;

        :nth-child(odd) {
          font-size: 1.3em;
          color: white;
          font-weight: 700;
        }

        :nth-child(even) {
          margin-top: 5px;
          font-size: 0.9rem;
          color: #bfc1c8;
        }
      }
    }
  }
}

