@import "./variables.scss";

* {
  box-sizing: border-box;
}

html {
  font-size: $font-size;
  font-family: sans-serif;
  font-size: 16px;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  outline: none;
  cursor: pointer;
}
